* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Microsoft Jhenghei"; }

.clearfix::after {
  content: "";
  display: block;
  clear: both; }

a {
  color: #000; }

img {
  width: 100%; }

.vam {
  font-size: 0; }

.vam::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%; }

.vam > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  float: none !important; }

.full-width {
  width: 100%; }

@media (min-width: 0) {
  .fixed-width {
    width: 100%;
    margin: auto; }
  .col-12 {
    width: 100%;
    float: left;
    padding: 15px; } }

@media (min-width: 600px) {
  .fixed-width {
    width: 600px;
    margin: auto; }
  .col-sm-4 {
    padding: 15px;
    width: 33.33333%;
    float: left; }
  .col-sm-6 {
    padding: 15px;
    width: 50%;
    float: left; } }

@media (min-width: 720px) {
  .fixed-width {
    width: 720px;
    margin: auto; } }

@media (min-width: 1024px) {
  .fixed-width {
    width: 1024px;
    margin: auto; } }

@media (min-width: 1200px) {
  .fixed-width {
    width: 1200px;
    margin: auto; } }

.bg-blue {
  background-color: #0772ff; }

.btn {
  display: inline-block;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  color: #fff;
  border: 0;
  cursor: pointer;
  outline: 0; }

.btn-red {
  background: #ff0040; }

.btn-blue {
  background: #0772ff; }

nav {
  padding-top: 12px;
  padding-bottom: 12px; }
  nav .logo {
    float: left; }
  nav .menu {
    float: right; }
    nav .menu a {
      border-radius: 5px; }
      nav .menu a:hover {
        background-color: #fff127;
        color: #000; }
  nav a {
    display: inline-block;
    padding: 6px 5px;
    text-decoration: none;
    color: #fff;
    transition: .3s; }

header {
  position: relative;
  height: 0;
  padding-bottom: 37.5%; }
  header .wrapper {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%; }
    header .wrapper .content {
      width: 600px; }

#about {
  padding-top: 50px;
  padding-bottom: 50px; }
  @media (min-width: 600px) {
    #about {
      padding-top: 100px;
      padding-bottom: 100px; } }

#service {
  background: #ffc4c4;
  padding-top: 50px;
  padding-bottom: 50px; }
  @media (min-width: 600px) {
    #service {
      padding-top: 100px;
      padding-bottom: 100px; } }

.form-control {
  margin-bottom: 15px;
  font-size: 0; }
  .form-control > * {
    font-size: 16px; }

input[type="text"] {
  border: 1px solid #999;
  padding: 10px;
  width: 100%;
  border-radius: 5px;
  outline: 0;
  transition: .3s;
  color: #fa0; }

label {
  width: 10%;
  display: inline-block;
  vertical-align: middle; }

.form-control div {
  display: inline-block;
  width: 90%;
  vertical-align: middle; }

input[type="text"]:focus {
  border: 1px solid #4aca00;
  background: #dddddd; }
